export const query = graphql`
query {
  allCvJson {
    totalCount
    edges {
      node {
        title
        yrStart
        yrEnd
        location
        org
        type
        descA
        descB
        bulletA
        bulletB
      }
    }
  }
  allCvHighLightJson {
    edges {
      node {
        type
        span
        note
        sum
        list
        heading
        bullet
      }
    }
  }
}`
